import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router } from '@angular/router';
import { MurmurService } from '../murmur.service';

@Component({
  selector: 'app-wisperchatlist',
  templateUrl: './wisperchatlist.component.html',
  styleUrls: ['./wisperchatlist.component.css']
})
export class WisperchatlistComponent implements OnInit, OnDestroy {

  ChatUserList: any = []
  tempList: any = [];
  i: any;
  interval1;
  interval2;
  temp_key1;
  selected_account = '';
  
  constructor(private router:Router,private ms:MurmurService) { }

  ngOnInit() {
    if(localStorage.getItem(this.ms.Scatter_account_name).split('-').length == 2)
      this.ms.getWhisperHomePage().then(res => {
        this.ChatUserList = res
        this.tempList = res;
        console.log(res)
        this.get_list()
        this.change_highlighter()
      })

  }

  select_User(index)
  {
    this.ms.setdata(this.ChatUserList[index]);
    this.router.navigate(['/userchat']);
  }

  change_highlighter()
  {
    this.interval2 = setInterval(() => {
      if(this.selected_account != this.ms.chatName.account_name )
      {
        this.selected_account = this.ms.chatName.account_name;
        
        for( this.i=0; this.i<this.ChatUserList.length;this.i++)
        {
          if(this.selected_account == this.ChatUserList[this.i].account_name)
            document.getElementById(this.i).setAttribute("style","border-left: 10px solid #a09fed;background: #171d2b;border-bottom: 1px solid #161c2b;");
          else
            document.getElementById(this.i).removeAttribute("style");
        }
      }
    }, 500);
  }

  somevalue(text){
    this.temp_key1 = text;
    if(text != ""){
      let temp_key2 = text;
      this.ms.getsearchProfile(text).then(res => {
        // this.ChatUserList = res;
        for(let i=0;i<res['length'];i++)
        {
          for(let j=0; j<this.tempList.length;j++)
          {
            if(res[i].account_name == this.tempList[j].account_name)
              res[i] = this.tempList[j]
          }
        }
        this.updatelist(temp_key2,res)
      })
      
    }
    else
    {
      this.ChatUserList = this.tempList
    }
  }

  updatelist(key, res)
  {
    if(key == this.temp_key1)
      this.ChatUserList = res;
  }

  get_list()
  {
    if(localStorage.getItem(this.ms.Scatter_account_name).split('-').length == 2)
    {
      let temp_list;
      this.interval1 = setInterval(()=>{
        this.ms.getWhisperHomePageList().then(res => {
          if(temp_list != JSON.stringify(res['data']))
          {
            console.log("found some updates")
            temp_list = JSON.stringify(res['data'])
            this.update_List_Data()
          }
        })
      },1000)
    }
  }

  update_List_Data()
  {
    console.log("inside the list updater")
    this.ms.getWhisperHomePage().then(resp => {
      let data = resp;
      let len = 0
      console.log("successfully got the updated msgs")

      if(this.ChatUserList.length != data['length'])
      {
        len = data['length'] - this.ChatUserList.length
        for(let i=0; i<len; i++)
        {
          console.log("append new user")
          this.ChatUserList.splice(i, i, data[i]);
        }
      }

      for(let i=len; i<data['length']; i++)
      {
        if(this.ChatUserList[0].account_name == data[i].account_name)
          if(this.ChatUserList[0].msg != data[i].msg)
            if(this.ChatUserList[0].Time.getTime() !== data[i].Time.getTime())
            {
              this.ChatUserList[0].msg = data[i].msg
              this.ChatUserList[0].Time = data[i].Time.getTime();
              break;
            }


        if(this.ChatUserList[i].account_name != data[i].account_name)
          if(this.ChatUserList[i].Name != data[i].Name)
            if(this.ChatUserList[i].msg != data[i].msg)
              if(this.ChatUserList[i].Time.getTime() !== data[i].Time.getTime())
              {
                for(let j=0;j<this.ChatUserList.length;j++)
                {
                  if(this.ChatUserList[j].account_name == data[i].account_name)
                  {
                    this.ChatUserList.splice(j,1);
                    this.ChatUserList.splice(0,0,data[i]);
                    break
                  }
                }
                break
              }
      }    

      this.selected_account = undefined;
    })
  } 

  ngOnDestroy() 
  {
    clearInterval(this.interval1)
    clearInterval(this.interval2)
    this.selected_account = undefined;
  }

}
