import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { MurmurService } from '../murmur.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router } from '@angular/router';

@Component({
  selector: 'app-commentpost',
  templateUrl: './commentpost.component.html',
  styleUrls: ['./commentpost.component.css']
})
export class CommentpostComponent implements OnInit {
  @ViewChild('closeBtn') closeBtn: ElementRef;

  Murmur_Commetnts: any = [];
  Murmur_Posts: any = [];
  My_Profile: any = {};
  cmt:any = '';
  resMes;

  constructor( private ms: MurmurService, private router:Router, private spin : NgxSpinnerService ) { }

  ngOnInit() {

    if(this.ms.Acc_Profile.length == 0)
    {
      this.ms.getAccountProfile(localStorage.getItem(this.ms.Scatter_account_name).split('-')[0]).then(res => {
        this.My_Profile = res[0];
      })
    }
    else
    {
      this.My_Profile = this.ms.Acc_Profile[0];
    }   

    this.ms.getCommentData().then(res => {
      if(res['id'] != undefined)
      {
        if(res['type'] == 'murmur')
        {
          // console.log("mur")
          this.ms.getMurmurByIndex(res['id']).then(mur => {
            this.Murmur_Posts.push(mur);
            // console.log(this.Murmur_Posts)
            if(this.Murmur_Posts[0].action_type == 'murmur')
            {
              this.ms.getMurmurComments(this.Murmur_Posts[0].murmur.murmur_id).then(resp => {
                this.Murmur_Commetnts = resp
                // console.log(res)
              })
            }
            else
            {
              this.ms.getYellComments(this.Murmur_Posts[0].yell.yell_id).then(resp => {
                this.Murmur_Commetnts = resp
                // console.log(res)
              })
            }
          })
        }
        else
        {
          // console.log("mymur")
          this.ms.getMyMurmurByIndex(res['id']).then(mur => {
            this.Murmur_Posts.push(mur);
            console.log(this.Murmur_Posts)
            this.ms.getMurmurComments(this.Murmur_Posts[0].murmur.murmur_id).then(resp => {
              this.Murmur_Commetnts = resp
              // console.log(res)
            })
          })
        }
      }
      else
      {
        this.router.navigate(['/']);
      }
    })    
  }

  cmt_ID(id)
  {
    this.ms.getCommentData().then(res => {
      this.ms.setCommentData(id, res['type'])
    })
  }

  snoop(id)
  {
    this.ms.getCommentData().then(res => {
      this.ms.Snoop(res['id'], res['type']).then(res => {
        console.log(res)
        if(res['Error'])
        {
          if(res['errMesg'] == undefined)
            res['errMesg'] = 'Internal Server Error'
          console.log(res['errMesg'])
          this.resMes = 'Error Message: '+res['errMesg']
          document.getElementById("openModalButton").click();
        }
        else
        {
          console.log(res['transId'])
          this.resMes = 'Your Snoop is submitted into blockchain' // 'Transaction ID: '+res['transId']
          document.getElementById("openModalButton").click();
          this.Murmur_Posts[0].murmur.snopped = 1
          this.Murmur_Posts[0].murmur.snoop_count += 1;
          // this.ms.setSnoopedMurmur(res['id'], res['type'])
        }
      })
    })
  }

  snooped()
  {
    this.resMes = 'You are already snooped'
    document.getElementById("openModalButton").click();
  }

  otherUser(name)
  {
    this.ms.setOtherUserName(name.split('@')[1])
    this.router.navigate([{outlets: {primary: ['userpost'], feed: ['userprofilelist']}}]);
  }

  comment()
  {
    this.spin.show()
    this.ms.getCommentData().then(res => {
      if(res['id'] != undefined)
      {
        let mur = [];
        if(res['type'] == 'murmur')
        {
          // console.log("mur")
          this.ms.getMurmurByIndex(res['id']).then(resp => {
            mur.push(resp)
            // console.log(mur)
            if(mur[0].action_type == 'murmur')
            {
              this.ms.commentmurmur(mur[0].murmur.murmur_id,this.cmt).then(res => {
                console.log(res);
                if(res['Error'])
                {
                  if(res['errMesg'] == undefined)
                    res['errMesg'] = 'Internal Server Error'
                  console.log(res['errMesg'])
                  this.resMes = 'Error Message: '+res['errMesg']
                  document.getElementById("openModalButton").click();
                }
                else
                {
                  console.log(res['transId'])
                  this.resMes = 'Your Comment is submitted into blockchain' // 'Transaction ID: '+res['transId']
                  document.getElementById("openModalButton").click();
                  this.cmt = ''
                }
              })
            }
            else
            {
              this.ms.commentyell(mur[0].yell.yell_id,this.cmt).then(res => {
                console.log(res);
                if(res['Error'])
                {
                  if(res['errMesg'] == undefined)
                    res['errMesg'] = 'Internal Server Error'
                  console.log(res['errMesg'])
                  this.resMes = 'Error Message: '+res['errMesg']
                  document.getElementById("openModalButton").click();
                }
                else
                {
                  console.log(res['transId'])
                  this.resMes = 'Your Comment is submitted into blockchain' // 'Transaction ID: '+res['transId']
                  document.getElementById("openModalButton").click();
                  this.cmt = ''
                }
              })
            }
          })
        }
        else
        {
          // console.log("mymur")
          this.ms.getMyMurmurByIndex(res['id']).then(resp => {
            mur.push(resp)
            console.log(mur)
            this.ms.commentmurmur(mur[0].murmur.murmur_id,this.cmt).then(res => {
              console.log(res);
              if(res['Error'])
                {
                  if(res['errMesg'] == undefined)
                    res['errMesg'] = 'Internal Server Error'
                  console.log(res['errMesg'])
                  this.resMes = 'Error Message: '+res['errMesg']
                  document.getElementById("openModalButton").click();
                }
                else
                {
                  console.log(res['transId'])
                  this.resMes = 'Your Comment is submitted into blockchain' // 'Transaction ID: '+res['transId']
                  document.getElementById("openModalButton").click();
                  this.cmt = ''
                }
            })
          })
        }
      }
      else
      {
        this.router.navigate(['/']);
      }
    })
  }

  goBack()
  {
    this.ms.goBack();
  }

}
