import { Component, OnInit, OnDestroy } from '@angular/core';
import { MurmurService } from '../murmur.service';

@Component({
  selector: 'app-userprofilelist',
  templateUrl: './userprofilelist.component.html',
  styleUrls: ['./userprofilelist.component.css']
})
export class UserprofilelistComponent implements OnInit, OnDestroy {

  My_Profile: any = {};
  name: any = '';
  interval;

  constructor( private ms: MurmurService ) { }

  ngOnInit() {
    this.interval = setInterval(() => {
      this.ms.getOtherUserName().then(res => 
      {
        if(this.name != res)
        {
          this.name = res;
          if(res == undefined)
          {
            if(this.ms.Acc_Profile.length == 0)
            {
              this.ms.getAccountDetail(localStorage.getItem(this.ms.Scatter_account_name).split('-')[0]).then(res => {
                this.My_Profile = res[0];
              })
            }
            else
            {
              this.My_Profile = this.ms.Acc_Profile[0];
            }
          }
          else
          {
            this.ms.getAccountDetail(res).then(res => {
              this.My_Profile = res[0];
              // console.log(res)
            })
          }
        }
      })

      if(this.ms.Acc_Profile.length != 0 && name == undefined)
        if(this.ms.Acc_Profile[0].following != this.My_Profile.following)
        {
          this.My_Profile.following = this.ms.Acc_Profile[0].following;
        }
    }, 50)
  }

  ngOnDestroy()
  {
    clearInterval(this.interval)
  }

  logout()
  {
    if(localStorage.getItem(this.ms.Scatter_account_name).split('-').length == 1)
      this.ms.Scater_logout().then().catch(e => {
        console.log("error")
        console.log(e)
      })
    else
      this.ms.PKey_logout().then().catch(e => {
        console.log("error")
        console.log(e)
      })
  }
}
