import { Component, OnInit } from '@angular/core';
import { MurmurService } from '../murmur.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router } from '@angular/router';

@Component({
  selector: 'app-followers',
  templateUrl: './followers.component.html',
  styleUrls: ['./followers.component.css']
})
export class FollowersComponent implements OnInit {
  Followers: any;
  resMes;
  load: boolean = false
  search_people;

  constructor( private ms: MurmurService, private router:Router, private spin : NgxSpinnerService ) { }

  ngOnInit() 
  {  
    this.spin.show();
    this.ms.getFollowersDetail().then(res => {
      if(res != undefined)
      {
        this.Followers = res;
        this.spin.hide()
        this.load = false
      }
      else
      {
        this.load = true;
        this.spin.hide()
        // this.resMes = 'There is no Followers found!'
        // document.getElementById("openModalButton").click();
      }
    })
  }

  follow(id, index, type)
  {
    id = id.split('@')[1]
    this.ms.follow(id).then(res => { 
      console.log(res)
      if(res['Error'])
      {
        if(res['errMesg'] == undefined)
          res['errMesg'] = 'Internal Server Error'
        console.log(res['errMesg'])
        this.resMes = 'Error Message: '+res['errMesg']
        document.getElementById("openModalButton").click();
      }
      else
      {
        console.log(res['transId'])
        this.resMes = 'Your Follow data is submitted into blockchain'
        document.getElementById("openModalButton").click();
        if(type == 1)
          this.Followers[index].status = true;
        else
        {
          this.search_people[index].status = true;
          for(let i=0; i< this.Followers.length; i++)
          {
            if(this.Followers[i].Acc_id == this.search_people[index].Acc_id)
              this.Followers[i].status = true;
          }
        }
        this.ms.Acc_Profile[0].following += 1
      }
    }).catch(err =>  {
      console.log(err)
    })
  }

  following(id, index, type)
  {
    id = id.split('@')[1]
    this.ms.unfollow(id).then(res => { 
      console.log(res)
      if(res['Error'])
      {
        if(res['errMesg'] == undefined)
          res['errMesg'] = 'Internal Server Error'
        console.log(res['errMesg'])
        this.resMes = 'Error Message: '+res['errMesg']
        document.getElementById("openModalButton").click();
      }
      else
      {
        console.log(res['transId'])
        this.resMes = 'Your Unfollow data is submitted into blockchain'
        document.getElementById("openModalButton").click();
        if(type == 1)
          this.Followers[index].status = false;
        else
        {
          this.search_people[index].status = false;
          for(let i=0; i< this.Followers.length; i++)
          {
            if(this.Followers[i].Acc_id == this.search_people[index].Acc_id)
              this.Followers[i].status = false;
          }
        }
        this.ms.Acc_Profile[0].following -= 1
      }
    }).catch(err =>  {
      console.log(err)
    })
  }

  search(text){
    this.ms.SearchPeople(text).then(res => {
      this.search_people = res;
    })
  }

  otherUser(name)
  {
    this.ms.setOtherUserName(name.split('@')[1])
    this.router.navigate([{outlets: {primary: ['userpost'], feed: ['userprofilelist']}}]);
  }

  goBack()
  {
    this.ms.goBack();
  }

}
