  // Using Private Key

  public async isPri_Valid(privKey): Promise<boolean>{
    const { ecc } = Eos.modules    
    return new Promise((resolve, reject) => {
      if(ecc.isValidPrivate(privKey))
        resolve(true)
      else
        resolve(false)
    })as Promise<boolean>;
  }

  public async PriToPub(privKey): Promise<any>{
    const { ecc } = Eos.modules
    return new Promise((resolve, reject) => {
      const pubKey = ecc.privateToPublic(privKey)
      resolve(pubKey)
    })as Promise<any>;
  }

  public async AccToPub(acc): Promise<any>{    
    return new Promise((resolve, reject) => {
      this.eos.getAccount(acc).then(res => {
        resolve(res.permissions[0].required_auth.keys[0].key)
      })
    })as Promise<any>;
  }

  public async PKey_login(acc_name,privKey): Promise<any>{

    const config = {
      chainId: 'e70aaab8997e1dfce58fbfac80cbbb8fecec7b99cf982a9444273cbc64c41473',
      httpEndpoint: 'http://jungle2.cryptolions.io:80',
      sign: true,
      broadcast: true,
    };
    const eos = Eos(config)
    
    let flag = 0;
    return new Promise((resolve, reject) => {
      this.PriToPub(privKey).then(pubKey => {
        eos.getKeyAccounts(pubKey, (error, result) => {
          if (error) reject("error")

          for(let i=0;i<result.account_names['length'];i++)
          {
            if(result.account_names[i] == acc_name)
            {
              localStorage.setItem(this.Scatter_account_name, result.account_names[i]+'-'+privKey)
              flag = 1;
              this.SetConfig().then(res => console.log("Configuration successed"))
              resolve(true)
            }
          }
          if(flag == 0)
            alert("Enter the valid account name")
        })
      })
    })as Promise<any>;
  }

  public async PKey_logout(): Promise<boolean> {
    return new Promise((resolve, reject) =>{
      localStorage.removeItem(this.Scatter_account_name);
      this.Acc_Profile = [];
      this.Murmur_Posts = [];
      this.router.navigate([{outlets: {primary: ['login'], feed: ['login']}}]);
      resolve(true)
    })as Promise<boolean>;
  }

  // Scatter

  public async Check_scatter(): Promise<boolean> {
    return new Promise((resolve, reject) =>{
      resolve(localStorage.getItem(this.Scatter_account_name) !== null);
    })as Promise<boolean>;
  }

  public async Scater_login(): Promise<boolean> {
    return new Promise((resolve, reject) =>{
      var my = this;
      my.scatter = (<any>window).scatter;

      if (my.identity) {
        my.scatter.useIdentity(my.identity.hash);
      }

      this.scatter.getIdentity({accounts:[my.network]}).then(
        function(identity){
          my.identity = identity;
          localStorage.setItem(my.Scatter_account_name, my.identity.accounts[0].name)
          my.Scatter_account_name = my.identity.accounts[0].name;
          my.scatter.useIdentity(my.identity.hash);
          resolve(true);
        }
      ).catch(error => {
        console.log(error);
        resolve(false);
      });
    })as Promise<boolean>;
  }

  public async Scater_logout(): Promise<boolean> {
    return new Promise((resolve, reject) =>{
      var my = this;
      my.scatter = (<any>window).scatter;

      if (my.identity) {
        my.scatter.useIdentity(my.identity.hash);
      }

      this.scatter.getIdentity({accounts:[my.network]}).then(
        function(identity){
          my.identity = identity;
          my.scatter.forgetIdentity()
          localStorage.removeItem(my.Scatter_account_name);
          my.Acc_Profile = [];
          my.Murmur_Posts = [];
          my.router.navigate([{outlets: {primary: ['login'], feed: ['login']}}]);
          resolve(true)
        }
      ).catch(error => {
        console.log(error);
        resolve(false);
      });
    })as Promise<boolean>;
  }

