  public async getFollowersDetail(): Promise<object> {
    return new Promise((resolve, reject) =>{
      let arr = [];

      this.getFollwers(localStorage.getItem(this.Scatter_account_name).split('-')[0]).then(res => {
        if(res)
        {
          for(let i=0; i<res['length'];i++)
          {
            this.getAccountProfile(res[i]).then(res => {
              if(res[0].profile_pic == "undefined")
                res[0].profile_pic = "assets/img/pic1.png"
    
              if(res[0].account_name == "")
                res[0].account_name = res[0].Acc_id.split('@')[1]
    
              arr.push({
                Acc_name: res[0].account_name,
                Acc_id: res[0].Acc_id,
                profile_pic: res[0].profile_pic,
                status: false
              })
            })
          }
    
          this.getFollwing(localStorage.getItem(this.Scatter_account_name).split('-')[0]).then(resp => {
            for(let i=0; i<resp['length'];i++)
              for(let j=0;j<arr.length;j++)
                if(resp[i] == arr[j].Acc_id.split('@')[1])
                  arr[j].status = true;
          })
          this.Followers = arr;
          resolve(arr);
        }
        else
        {
          resolve(undefined);
        }
      })
    })as Promise<object>;
  }

