  public async getMyMurmurs(acc_name): Promise<object> {
    return new Promise((resolve, reject) =>{
      let temparray = [];
      let name, img;
      this.spin.show();
      this.http
        .get<{ message: string; posts: any }>(
          "http://murmurjapi.wandx.co/api/getMurmursByAccountName?account_name=" + acc_name
        )
        .subscribe( postData => {
          var mur = Object.values(postData)[1]
          for(var i=0; i<mur.length; i++)
          {
            let mur_url = mur[i].attached_url
            if(mur_url.split('.').length == 1 && mur_url !== '')
              mur[i].attached_url = 'https://ipfs.io/ipfs/' + mur_url;

            if(this.Other_User_name)
            {
              if(this.Others_Acc_Profile.length == 0)
              {
                this.getAccountProfile(this.Other_User_name).then(resp =>{
                  name = resp[0].account_name
                  img = resp[0].profile_pic
                })
              }
              else
              {
                name = this.Others_Acc_Profile[0].account_name
                img = this.Others_Acc_Profile[0].profile_pic
              }
            }
            else
            {
              if(this.Acc_Profile.length == 0)
              {
                this.getAccountProfile(acc_name).then(resp =>{
                  name = resp[0].account_name
                  img = resp[0].profile_pic
                })
              }
              else
              {
                name = this.Acc_Profile[0].account_name
                img = this.Acc_Profile[0].profile_pic
              }
            }

            temparray.push({
              id : mur[i]._id,
              action_type: 'murmur',
              account_name: name,
              Acc_id: '@'+mur[i].account_name,
              image: img,
              murmur: {
                murmur_id: mur[i].murmur_id,
                message: mur[i].message,
                attached_url: mur[i].attached_url,
                visibility: mur[i].visibility,
                yell_count: mur[i].yell_count,
                comment_count: mur[i].comment_count,
                snoop_count: mur[i].snoop_count,
                // snooped: mur[i].snooped,
              },
              createdAt: mur[i].createdAt,
            })
          }
          this.My_Murmur_Posts = temparray.reverse()
          // console.log(this.My_Murmur_Posts)
          resolve(this.My_Murmur_Posts);
          this.spin.hide();
        })
    })as Promise<object>;
  }

  public async getMurmurComments(id): Promise<object> {
    return new Promise((resolve, reject) =>{
      let my = []
      this.http
      .get<{ message: string; posts: any }>(
        "http://murmurjapi.wandx.co/api/getCommentsByMurmurId?murmur_id="+id
      )
      .subscribe(postData => {
        var mur = Object.values(postData)[1].comments
        for(var i=0; i<mur.length; i++)
        {            
          my.push({
            id : mur[i]._id,
            comment_id: mur[i].comment_id,
            account_name: mur[i].account_name,
            Acc_id: '@'+mur[i].account_name,
            image: this.image,
            comment: mur[i].comment,
            createdAt: mur[i].createdAt,
            yell: {}
          })            
        }

        // console.log(my)

        for(let i=0; i<my.length;i++)
        {
          this.getAccountProfile(my[i].account_name).then(res => {
            // console.log(res)
            my[i].account_name = res[0].account_name;
            my[i].image = res[0].profile_pic;
          })
          // console.log(my)
        }
      })
      
      // console.log(my)
      this.Murmur_Commetnts = my;
      resolve(this.Murmur_Commetnts);
       
    })as Promise<object>;
  }

