  public async getAccountProfile(acc_name): Promise<object> {
    return new Promise((resolve, reject) =>{
      let flag = 0;
      for(let i=0;i<this.Others_Profile.length; i++)
      {
        if('@'+acc_name == this.Others_Profile[i].Acc_id)
        {
          flag = 1;
          let temparr = [];
          temparr.push(this.Others_Profile[i])
          resolve(temparr)
        }
      }

      if(flag == 0)
      {
        this.getAccountInfo(acc_name).then(res => {
          resolve(res)
        })
      }
       
    })as Promise<object>;
  }

  public async getAccountInfo(acc_name): Promise<object> {
    return new Promise((resolve, reject) =>{
      var temparray = []

      this.http
      .get<{ message: string; posts: any }>(
        "http://murmurjapi.wandx.co/api/getAccountByID?account_name=" + acc_name 
      )
      .subscribe(postData => {
        var mur = Object.values(postData)[1]

        if(mur.account_name == undefined || (mur.name == undefined && mur.profile_pic == undefined))
        {
          mur.account_name = acc_name
          mur.name = acc_name
          mur.profile_pic = this.image
        }

        if(mur.profile_pic == '')
          mur.profile_pic = this.image

        if(mur.name == "")
          mur.name = mur.account_name

        temparray.push({
          account_name:  mur.name,
          Acc_id: '@'+mur.account_name,
          profile_pic: mur.profile_pic,
        })
        
        this.Others_Profile.push(temparray[0]);
        resolve(temparray);
      })
       
    })as Promise<object>;
  }

  public async getAccountDetail(acc_name): Promise<object> {
    return new Promise((resolve, reject) =>{
      var temparray = []

      this.http
      .get<{ message: string; posts: any }>(
        "http://murmurjapi.wandx.co/api/getAccountByID?account_name=" + acc_name
      )
      .subscribe(postData => {
        var mur = Object.values(postData)[1]

        if(mur.account_name == undefined || (mur.name == undefined && mur.profile_pic == undefined))
        {
          mur.account_name = acc_name
          mur.name = acc_name
          mur.profile_pic = this.image
        }

        if(mur.profile_pic == '' || mur.profile_pic == undefined || mur.profile_pic == 'undefined' )
          mur.profile_pic = this.image

        if(mur.name == "" || mur.name == undefined)
          mur.name = mur.account_name

        temparray.push({
          id : mur._id,
          account_name:  mur.name,
          Acc_id: '@'+mur.account_name,
          bio: mur.bio,
          dob: mur.dob,
          location: mur.location,
          profile_pic: mur.profile_pic,
          sex: mur.sex,
          murmurs: 0,
          followers: 0,
          following: 0
        })

        this.getAccountSummary(acc_name).then(resp => {
          if(resp['length'] != 0)
          {
            temparray[0].murmurs = resp[0].murmurs;
            temparray[0].followers = resp[0].followers;
            temparray[0].following = resp[0].following;
          }
        })

        if(localStorage.getItem(this.Scatter_account_name))
          if(acc_name == localStorage.getItem(this.Scatter_account_name).split('-')[0])
            this.Acc_Profile = temparray;
          else
            this.Others_Acc_Profile = temparray;
        
        resolve(temparray);
      })
       
    })as Promise<object>;
  }

