  public async getWhisperHomePage() : Promise<object>{
    return new Promise((resolve,reject) => {
      this.getWhisperHomePageList().then(response => {
        var testarray = [];
        let acc;
        
        for(let i=0;i<Object.values(response)[1].length;i++)
        {
          var encrypted_message = JSON.parse(Object.values(response)[1][i].message);
          var { ecc } = Eos.modules;
          var decrypted_message;
          var mess;
          var pub

          if(Object.values(response)[1][i].from_account_name == localStorage.getItem(this.Scatter_account_name).split('-')[0])
          {
            acc = Object.values(response)[1][i].to_account_name
            pub = Object.values(response)[1][i].to_public_key
          }
          else
          {
            acc = Object.values(response)[1][i].from_account_name
            pub = Object.values(response)[1][i].from_public_key
          }

          decrypted_message = ecc.Aes.decrypt(localStorage.getItem(this.Scatter_account_name).split('-')[1], pub, encrypted_message.nonce, encrypted_message.message, encrypted_message.checksum);
          mess = decrypted_message.toString();

          if(mess.toString().length > 20)
          {
            mess = mess.slice(0,20)+'...'
          }

          testarray.push({
            msg : mess,
            Time : new Date(Object.values(response)[1][i].createdAt),
            account_name : acc,
            Name: '',
            profile_pic: ''
          });

          for(let i=0; i<testarray.length;i++)
          {
            this.getAccountProfile(testarray[i].account_name).then(res => {
              // console.log(res)
              testarray[i].Name = res[0].account_name;
              testarray[i].profile_pic = res[0].profile_pic;
            })
            // console.log(my)
          }
        }
        
        this.ChatHomePage = testarray;
        // console.log(testarray)
        resolve(this.ChatHomePage);
        
      })
    })as Promise<object>;
  }

