  public async getChatDetails(from_acc,to_acc) : Promise<object>{
    return new Promise((resolve,reject) => {
      // console.log(from_acc, to_acc)
      this.getChatCount(from_acc, to_acc).then(response => {
        var testarray = [];
        let temp = response['data']
        for(let i=0;i<temp.length;i++)
        {
                   
          var { ecc } = Eos.modules;
          var decrypted_message;
          var mess;

          testarray.push({
            block_number : temp[i].block_number,
            whisper_id :  temp[i].whisper_id,
            from_public_key : temp[i].from_public_key,
            to_public_key : temp[i].to_public_key,
            message : '',
            createdAt : new Date(temp[i].createdAt),
            from_account_name : '',
            to_account_name : ''
          });
        }

        // console.log(testarray)

        for(let i=0; i<testarray.length; i++)
        {
          this.AccToPub(from_acc).then(res => {
            if(res == temp[i].from_public_key)
            {
              testarray[i].from_account_name = from_acc
              testarray[i].to_account_name = to_acc
              var encrypted_message = JSON.parse(temp[i].message);
              decrypted_message = ecc.Aes.decrypt(localStorage.getItem(this.Scatter_account_name).split('-')[1],temp[i].to_public_key, encrypted_message.nonce, encrypted_message.message, encrypted_message.checksum);
              mess = decrypted_message.toString();
              testarray[i].message = mess;
            }
            else
            {
              testarray[i].from_account_name = to_acc
              testarray[i].to_account_name = from_acc
              var encrypted_message = JSON.parse(temp[i].message);
              decrypted_message = ecc.Aes.decrypt(localStorage.getItem(this.Scatter_account_name).split('-')[1],temp[i].from_public_key, encrypted_message.nonce, encrypted_message.message, encrypted_message.checksum);
              mess = decrypted_message.toString();
              testarray[i].message = mess;
            }  
          })
          // console.log(testarray)
        }

        this.User_both_list = testarray;
        // console.log(testarray)
        resolve(this.User_both_list);
      })
    })as Promise<object>;
  }

