  // Contract Calls

  public async postMurmur(post, url): Promise<object> {
    return new Promise((resolve, reject) =>{
      console.log("Post: " + post + ", URL: " + url);
      if(localStorage.getItem(this.Scatter_account_name).split('-').length == 1)
      {
        const scatter = (<any>window).scatter;
        scatter.getIdentity({accounts:[this.network]}).then(() => {

          const account = scatter.identity.accounts.find(x => x.blockchain === 'eos');

          const eosOptions = { expireInSeconds:60 };
          const eos = scatter.eos(this.network, Eos, eosOptions);

          const transactionOptions = { authorization:[`${account.name}@${account.authority}`] };

          eos.transaction('murmurdappco', contract => {
              contract.murmur(account.name, post, url, 1, transactionOptions);
            }).then(trx => {
              // console.log(`Transaction ID: ${trx.transaction_id}`);
              resolve({Error: false, transId: trx.transaction_id})
            }).catch(error => {
              console.error("Error in cont: ",error);
              resolve({Error: true, errMesg: error.message})
              // resolve(error.message)
            });

        }).catch(error => {
            console.error("Error in Scatter: ",error);
            reject(error)
            resolve()
        }); 
      }
      else
      {
        this.SetConfig().then(eos => {
          eos.transaction('murmurdappco', contract => {
            contract.murmur(localStorage.getItem(this.Scatter_account_name).split('-')[0], post, url, 1)
          }, this.config).then(trx => {
            // console.log(`Transaction ID: ${trx.transaction_id}`);
            resolve({Error: false, transId: trx.transaction_id})
          }).catch(error => {
            console.error(error);
            resolve({Error: true, errMesg: error.message})
            // reject(error);
          });
        })
      }
      
    })as Promise<object>;
  }

