  public async follow(facc): Promise<object> {
    return new Promise((resolve, reject) =>{
      if(localStorage.getItem(this.Scatter_account_name).split('-').length == 1)
      {
        const scatter = (<any>window).scatter;
        scatter.getIdentity({accounts:[this.network]}).then(() => {

          const account = scatter.identity.accounts.find(x => x.blockchain === 'eos');

          const eosOptions = { expireInSeconds:60 };
          const eos = scatter.eos(this.network, Eos, eosOptions);

          const transactionOptions = { authorization:[`${account.name}@${account.authority}`] };

          eos.transaction('murmurdappco', contract => {
            contract.follow(account.name, facc, 1, transactionOptions); 
          }).then(trx => {
            // console.log(`Transaction ID: ${trx.transaction_id}`);
            resolve({Error: false, transId: trx.transaction_id})
          }).catch(error => {
            console.error(error);
            resolve({Error: true, errMesg: error.message})
            // reject(error);
          });   

        }).catch(error => {
            console.error("Error in Scatter: ",error);
            reject(error)
        }); 
      }
      else
      {
        this.SetConfig().then(eos => {
          eos.transaction('murmurdappco', contract => {
            contract.follow(localStorage.getItem(this.Scatter_account_name).split('-')[0], facc, 1)
          }, this.config).then(trx => {
            // console.log(`Transaction ID: ${trx.transaction_id}`);
            resolve({Error: false, transId: trx.transaction_id})
          }).catch(error => {
            console.error(error);
            resolve({Error: true, errMesg: error.message})
            reject(error);
          });
        })
      }
       
    })as Promise<object>;
  }

  public async unfollow(facc): Promise<object> {
    return new Promise((resolve, reject) =>{
      if(localStorage.getItem(this.Scatter_account_name).split('-').length == 1)
      {
        const scatter = (<any>window).scatter;
        scatter.getIdentity({accounts:[this.network]}).then(() => {

          const account = scatter.identity.accounts.find(x => x.blockchain === 'eos');

          const eosOptions = { expireInSeconds:60 };
          const eos = scatter.eos(this.network, Eos, eosOptions);

          const transactionOptions = { authorization:[`${account.name}@${account.authority}`] };

          eos.transaction('murmurdappco', contract => {
            contract.unfollow(account.name, facc, transactionOptions);
          }).then(trx => {
            // console.log(`Transaction ID: ${trx.transaction_id}`);
            resolve({Error: false, transId: trx.transaction_id})
          }).catch(error => {
            console.error(error);
            resolve({Error: true, errMesg: error.message})
            // reject(error);
          });   

        }).catch(error => {
            console.error("Error in Scatter: ",error);
            reject(error)
        }); 
      }
      else
      {
        this.SetConfig().then(eos => {
          eos.transaction('murmurdappco', contract => {
            contract.unfollow(localStorage.getItem(this.Scatter_account_name).split('-')[0], facc)
          }, this.config).then(trx => {
            // console.log(`Transaction ID: ${trx.transaction_id}`);
            resolve({Error: false, transId: trx.transaction_id})
          }).catch(error => {
            console.error(error);
            resolve({Error: true, errMesg: error.message})
            // reject(error);
          });
        })
      }
       
    })as Promise<object>;
  }

  public async whisper(to_account_name, message): Promise<object> {
    return new Promise((resolve, reject) =>{
      this.AccToPub(localStorage.getItem(this.Scatter_account_name).split('-')[0]).then(myPub => {

        this.AccToPub(to_account_name).then(toPub => {
          const { ecc } = Eos.modules
          let encrypted_message = ecc.Aes.encrypt(localStorage.getItem(this.Scatter_account_name).split('-')[1], toPub, message);
          encrypted_message.nonce = encrypted_message.nonce.toString();
          encrypted_message.message = encrypted_message.message.toString("binary");
          encrypted_message = JSON.stringify(encrypted_message);

          console.log(localStorage.getItem(this.Scatter_account_name).split('-')[0], to_account_name, myPub, toPub, encrypted_message)

          if(localStorage.getItem(this.Scatter_account_name).split('-').length == 2)
          {
            this.SetConfig().then(eos => {
              eos.transaction('murmurdappco', contract => {
                contract.whisper(localStorage.getItem(this.Scatter_account_name).split('-')[0], to_account_name, myPub, toPub, encrypted_message);              
              }, this.config).then(trx => {
                // console.log(`Transaction ID: ${trx.transaction_id}`);
                resolve({Error: false, transId: trx.transaction_id})
              }).catch(error => {
                console.error(error);
                resolve({Error: true, errMesg: error.message})
                // reject(error);
              });
            })
          }
        })
      })
       
    })as Promise<object>;
  }
  
