import { Component, OnInit, OnDestroy } from '@angular/core';
import { MurmurService } from '../murmur.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-userchat',
  templateUrl: './userchat.component.html',
  styleUrls: ['./userchat.component.css']
})
export class UserchatComponent implements OnInit, OnDestroy {
  public name: any = { Name : "", profile_pic: "", account_name:"", Time:"" };
  public mesg;
  public receive = new Array();
  public whisper : any = [] 
  public My_Profile : any;
  public intravel1
  public intravel2
  public resMes;

  constructor(private ms:MurmurService, private router:Router) { }
  
  ngOnInit() {

    this.My_Profile = localStorage.getItem(this.ms.Scatter_account_name).split('-')[0]
        
    this.intravel1 = setInterval(() => {
      if(this.name.account_name != this.ms.chatName.account_name)
      {
        this.name = this.ms.chatName;
        
        if(localStorage.getItem(this.ms.Scatter_account_name).split('-').length == 2)
          this.ms.getChatDetails(this.My_Profile,this.name.account_name).then(res => {
            // console.log(res)
            clearInterval(this.intravel2)
            this.whisper = res;
            this.get_fequent_msg(this.name.account_name, res['length'])
          })
      }
    }, 1000);
  }

  get_fequent_msg(acc, len)
  {
    // console.log(len, acc)
    this.intravel2 = setInterval(() => {
      this.ms.getChatCount(this.My_Profile,acc).then(res => {
        if(res['data']['length'])
        {
          // console.log(this.My_Profile,acc,this.whisper.length,res['data']['length'])
          if(len != res['data']['length'])
          {
            this.ms.getChatDetails(this.My_Profile,acc).then(res => {
              len = res['length']
              this.whisper.splice(0, 0, res[0]);
            })
          }
        }
      })
    },1000)
  }

  send_whisper()
  {
    if(localStorage.getItem(this.ms.Scatter_account_name).split('-').length == 2)
    {
      if(this.name.Name != "" && this.name.Name != undefined)
      {
        let temp_msg = this.mesg;
        this.mesg = undefined;
        if(temp_msg != undefined && temp_msg.trim() != '')
          this.ms.whisper(this.name.account_name, temp_msg).then(res => {
            console.log(res)
            if(res['Error'])
            {
              if(res['errMesg'] == undefined)
                res['errMesg'] = 'Internal Server Error'
              console.log(res['errMesg'])
              this.resMes = 'Error Message: '+res['errMesg']
              document.getElementById("openModalButton").click();
            }
            else
            {
              console.log(res['transId'])
              this.resMes = 'Your whisper is submitted into blockchain';
              document.getElementById("openModalButton").click();
            }
          })
        else
        {
          this.resMes = 'Please Enter the message!'
          document.getElementById("openModalButton").click();
        }
      }
      else
      {
        this.resMes = 'Please choose any receiver account'
        document.getElementById("openModalButton").click();
      }
    }
    else
    {
      this.resMes = 'Its only working in Privatekey login'
      document.getElementById("openModalButton").click();
    }
  }

  otherUser(name)
  {
    this.ms.setOtherUserName(name)
    this.router.navigate([{outlets: {primary: ['userpost'], feed: ['userprofilelist']}}]);
    this.ms.chatName.account_name = undefined;
  }

  goBack()
  {
    this.ms.goBack();
  }

  ngOnDestroy()
  {
    clearInterval(this.intravel1)
    clearInterval(this.intravel2)
    this.ms.chatName.account_name = undefined;
  }

}