import { Component, OnInit } from '@angular/core';
import { MurmurService } from '../murmur.service';
import { HttpClient } from '@angular/common/http';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
  selector: 'app-postmurmur',
  templateUrl: './postmurmur.component.html',
  styleUrls: ['./postmurmur.component.css']
})
export class PostmurmurComponent implements OnInit {

  murmur: any = '';
  public selectedFile: any = {};
  File_select: boolean;
  My_Profile: any = {};
  display='none';
  errormsg:boolean = false;
  errorcomments:string = "Max 256 charecters only allowed";
  resMes;
  url: any = "";
  fileName = "";

  constructor( private ms: MurmurService, private http:HttpClient, private spin : NgxSpinnerService ) { }

  ngOnInit() {

    if(this.ms.Acc_Profile.length == 0)
    {
      this.ms.getAccountProfile(localStorage.getItem(this.ms.Scatter_account_name).split('-')[0]).then(res => {
        this.My_Profile = res[0];
      })
    }
    else
    {
      this.My_Profile = this.ms.Acc_Profile[0];
    }
  }

  onFileChanged(event){
    this.selectedFile = {};
    this.selectedFile = event.target.files[0];
    this.File_select = true;

    this.fileName = this.selectedFile['name'];
    if(this.fileName.length > 35)
    {
      this.fileName = this.fileName.slice(0,30) + '.....' + this.fileName.slice(this.fileName.length-10, this.fileName.length)
    }

    var reader = new FileReader();
    reader.onload = (event: ProgressEvent) => {
      this.url = (<FileReader>event.target).result;
    }
    reader.readAsDataURL(event.target.files[0]);
  }

  file_clear()
  {
    this.selectedFile = {};
    this.File_select = false;
    this.url = ""
    this.fileName = ""
    console.log("Cleared")
  }

  post()
  {
    if(this.File_select && this.murmur.trim() != '')
    {
      this.spin.show()
      let fd = new FormData();
      fd.append('path', this.selectedFile);
      this.http.post("https://ipfs.infura.io:5001/api/v0/add?stream-channels=true",fd).subscribe((r)=>{
        console.log(r['Hash']);
        if(this.murmur == '') this.murmur = "";
        this.ms.postMurmur(this.murmur,r['Hash']).then(res => { 
          console.log(res)
          if(res['Error'])
          {
            if(res['errMesg'] == undefined)
              res['errMesg'] = 'Internal Server Error'
            console.log(res['errMesg'])
            this.spin.hide()
            this.resMes = 'Error Message: '+res['errMesg']
            document.getElementById("openModalButton").click();
          }
          else
          {
            console.log(res['transId'])
            this.selectedFile = {};
            this.File_select = false;
            this.murmur = ''
            this.url = ''
            this.spin.hide()
            this.resMes = 'Your Murmur is submitted into blockchain'
            document.getElementById("openModalButton").click();
          }
        }).catch(err =>  {
          console.log(err)
        })
      })
    }
    else
    {
      if(this.murmur.trim() != '')
      {
        this.spin.show()
        this.ms.postMurmur(this.murmur,'').then(res => { 
          console.log(res)

          if(res['Error'])
          {
            if(res['errMesg'] == undefined)
              res['errMesg'] = 'Internal Server Error'
            console.log(res['errMesg'])
            this.spin.hide()
            this.resMes = 'Error Message: '+res['errMesg']
            document.getElementById("openModalButton").click();
          }
          else
          {
            console.log(res['transId'])
            this.spin.hide()
            this.resMes = 'Your Murmur is submitted into blockchain'
            document.getElementById("openModalButton").click();
            this.murmur = ''
          }
        })
      }
      else
        this.errormsg = true
        this.errorcomments = "Please enter feeds description"
    }
  }

  create()
  {
    if(this.murmur.toString().length > 256)
    {
        this.errormsg = true;
        this.errorcomments = "Max 256 charecters only allowed"
    } else {
      this.errormsg = false;
    }
  }

  goBack()
  {
    this.ms.goBack();
  }
  
}
