import { Component, OnInit, OnDestroy } from '@angular/core';
import { MurmurService } from '../murmur.service';

@Component({
  selector: 'app-mainappone',
  templateUrl: './mainappone.component.html',
  styleUrls: ['./mainappone.component.css']
})
export class MainapponeComponent implements OnInit, OnDestroy {
  My_Profile: any = {};
  login:boolean;
  interval;

  constructor(private ms: MurmurService) { }

  ngOnInit() {
    this.interval = setInterval(()=>{
      this.ms.Check_scatter().then(result => {
        if(this.login != result)
        {
          this.login = result
          if(result)
          {
            if(this.ms.Acc_Profile.length == 0)
            {
              this.ms.getAccountProfile(localStorage.getItem(this.ms.Scatter_account_name).split('-')[0]).then(res => {
                this.My_Profile = res[0];
              })
            }
            else
            {
              this.My_Profile = this.ms.Acc_Profile[0];
            }
          }
          else
          {
            this.My_Profile = [];
          }
        }
      })
    },50)
  }

  otherUser()
  {
    this.ms.setOtherUserName(undefined)
  }

  logout()
  {
    this.ms.Scater_logout()      
  }

  ngOnDestroy()
  {
    clearInterval(this.interval)
  }

}
