import { Component, OnInit, OnDestroy } from '@angular/core';
import { MurmurService } from '../murmur.service';

@Component({
  selector: 'app-userpersonalfeed',
  templateUrl: './userpersonalfeed.component.html',
  styleUrls: ['./userpersonalfeed.component.css']
})
export class UserpersonalfeedComponent implements OnInit, OnDestroy {

  My_Murmur_Posts: any = [];
  My_Profile: any = {};
  name: any = '';
  resMes;
  load: boolean = false
  interval;

  constructor(private ms: MurmurService) { }

  ngOnInit() {

    this.interval = setInterval(() => {
      this.ms.getOtherUserName().then(res => 
      {
        if(this.name != res)
        {
          this.name = res;
          if(res == undefined)
          {
            if(this.ms.Acc_Profile.length == 0)
            {
              this.ms.getAccountDetail(localStorage.getItem(this.ms.Scatter_account_name).split('-')[0]).then(res => {
                this.My_Profile = res[0];
              })
            }
            else
            {
              this.My_Profile = this.ms.Acc_Profile[0];
            }
            
            this.ms.getMyMurmurs(localStorage.getItem(this.ms.Scatter_account_name).split('-')[0]).then(resp => {
              this.My_Murmur_Posts = resp
              if(this.My_Murmur_Posts.length == 0)
              {
                this.load = true
                // this.resMes = 'There is no more feeds! Please create any murmur!'
                // document.getElementById("openModalButton").click();
              }
              else
                this.load = false
              // console.log(res)
            })
          }
          else
          {
            this.ms.getAccountProfile(res).then(res => {
              this.My_Profile = res[0];
              // console.log(res)
            })
            
            this.ms.getMyMurmurs(res).then(resp => {
              this.My_Murmur_Posts = resp
              if(this.My_Murmur_Posts.length == 0)
              {
                this.load = true
                // this.resMes = 'There is no more feeds found!'
                // document.getElementById("openModalButton").click();
              }
              else
                this.load = false
              // console.log(res)
            })
          }
        }
      })
    }, 50)
  }

  snoop(id)
  {
    this.ms.Snoop(id, 'mymurmur').then(res => {
      console.log(res)
      if(res['Error'])
      {
        if(res['errMesg'] == undefined)
          res['errMesg'] = 'Internal Server Error'
        console.log(res['errMesg'])
        this.resMes = 'Error Message: '+res['errMesg']
        document.getElementById("openModalButton").click();
      }
      else
      {
        console.log(res['transId'])
        this.resMes = 'Your Snoop is submitted into blockchain'
        document.getElementById("openModalButton").click();
      }
    })
  }

  cmt_Data(id)
  {
    this.ms.setCommentData(id, 'mymurmur')
  }

  otherUser()
  {
    this.ms.setOtherUserName(undefined)
    this.ms.goBack();
  }

  ngOnDestroy()
  {
    clearInterval(this.interval)
  }

}
