import { Component, OnInit, OnDestroy } from '@angular/core';
import { MurmurService } from '../murmur.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-feed',
  templateUrl: './feed.component.html',
  styleUrls: ['./feed.component.css']
})
export class FeedComponent implements OnInit, OnDestroy {

  murmur: any = '';
  public selectedFile: any = {};
  File_select: boolean;
  load: boolean = false
  resMes;
  search_people;
  Murmur_Posts: any = [];
  scroll_height: any;
  flag: any = 0;
  last_resp: any = 1;
  interval;

  notificationMessage:string = "Load Feed"

  constructor( private ms: MurmurService, private router:Router ) { }

  ngOnInit() {
    this.load = false;

    if(this.ms.Followers.length == 0)
      this.ms.getFollowersDetail();

    if(this.ms.Murmur_Posts.length == 0)
    {
      this.Murmur_Posts = [];
      this.ms.getMurmurs().then(res => {
        this.Murmur_Posts = res
        ++this.ms.page_count
        this.load = true;

        if (res["length"] < 10) {
          this.notificationMessage = "No more feeds found"
        }
      })
    }
    else
    {
      this.Murmur_Posts = this.ms.Murmur_Posts
      this.load = true;
    }
  }

  snoop(id)
  {
    this.ms.Snoop(id, 'murmur').then(res => {
      console.log(res)
      if(res['Error'])
      {
        if(res['errMesg'] == undefined)
          res['errMesg'] = 'Internal Server Error'
        console.log(res['errMesg'])
        this.resMes = 'Error Message: '+res['errMesg']
        document.getElementById("openModalButton").click();
      }
      else
      {
        console.log(res['transId'])
        this.resMes = 'Your Snoop is submitted into blockchain'
        document.getElementById("openModalButton").click();
        if(this.Murmur_Posts[id].action_type == 'murmur')
        {
          this.Murmur_Posts[id].murmur.snooped = 1;
          this.Murmur_Posts[id].murmur.snoop_count += 1;
        }
        else
        {
          this.Murmur_Posts[id].yell.snooped = 1;
          this.Murmur_Posts[id].yell.snoop_count += 1; 
        }
      }
    })
  }

  snooped()
  {
    this.resMes = 'You are already snooped'
    document.getElementById("openModalButton").click();
  }

  cmt_Data(id)
  {
    this.ms.setCommentData(id, 'murmur')
  }

  otherUser(name)
  {
    this.ms.setOtherUserName(name.split('@')[1])
    this.router.navigate([{outlets: {primary: ['userpost'], feed: ['userprofilelist']}}]);
  }

  search(text){
    this.ms.SearchPeople(text).then(res => {
      this.search_people = res;
    })
  }

  follow(id, index)
  {
    id = id.split('@')[1]
    this.ms.follow(id).then(res => { 
      console.log(res)
      if(res['Error'])
      {
        if(res['errMesg'] == undefined)
          res['errMesg'] = 'Internal Server Error'
        console.log(res['errMesg'])
        this.resMes = 'Error Message: '+res['errMesg']
        document.getElementById("openModalButton").click();
      }
      else
      {
        console.log(res['transId'])
        this.resMes = 'Your Follow data is submitted into blockchain'
        document.getElementById("openModalButton").click();
        this.search_people[index].status = true;
      }
    }).catch(err =>  {
      console.log(err)
    })
  }

  following(id, index)
  {
    id = id.split('@')[1]
    this.ms.unfollow(id).then(res => { 
      console.log(res)
      if(res['Error'])
      {
        if(res['errMesg'] == undefined)
          res['errMesg'] = 'Internal Server Error'
        console.log(res['errMesg'])
        this.resMes = 'Error Message: '+res['errMesg']
        document.getElementById("openModalButton").click();
      }
      else
      {
        console.log(res['transId'])
        this.resMes = 'Your Unfollow data is submitted into blockchain'
        document.getElementById("openModalButton").click();
        this.search_people[index].status = false;
      }
    }).catch(err =>  {
      console.log(err)
    })
  }

  LoadFeeds() {
    if(this.notificationMessage != "No more feeds found")
    {
      var meta = this;
      meta.ms.getOtherPageMurmurs().then(res => {
        meta.last_resp = res['length']
        if(res['length'] !== 0)
        {
          ++meta.ms.page_count;
          for(let i=0; i<res['length']; i++) {
            meta.Murmur_Posts.push(res[i])
          }
        } else if(res['length'] === 0) {
          this.notificationMessage = "No Feeds Found"
          this.interval = setInterval(()=> {
            if(meta.last_resp == 0)
              meta.load = false
          }, 50)
        }
      })
    }
    // else
    // {
    //   this.resMes = 'Sorry there is no more feeds!'
    //   document.getElementById("openModalButton").click();
    // }
  }

  ngOnDestroy()
  {
    clearInterval(this.interval)
  }

}
