import { Component, OnInit } from '@angular/core';
import { MurmurService } from '../murmur.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router } from '@angular/router';

@Component({
  selector: 'app-following',
  templateUrl: './following.component.html',
  styleUrls: ['./following.component.css']
})
export class FollowingComponent implements OnInit {
  Following: any;
  resMes;
  load: boolean = false
  search_people;

  constructor( private ms: MurmurService, private router:Router, private spin : NgxSpinnerService ) { }

  ngOnInit() 
  {  
    this.fetch_Data()
  }

  fetch_Data()
  {
    this.spin.show();
    this.ms.getFollwing(localStorage.getItem(this.ms.Scatter_account_name).split('-')[0]).then(res => {
      if(res != undefined)
      {
        let arr = [];
        for(let i=0; i<res['length'];i++)
        {
          this.ms.getAccountProfile(res[i]).then(res => {
            if(res[0].profile_pic == "undefined")
            {
              res[0].profile_pic = "assets/img/pic1.png"
            }

            if(res[0].account_name == "")
            {
              res[0].account_name = res[0].Acc_id.split('@')[1]
            }

            arr.push({
              Acc_name: res[0].account_name,
              Acc_id: res[0].Acc_id,
              profile_pic: res[0].profile_pic,
              status: true,
            })
          })
        }
        this.Following = arr;
        this.spin.hide();
        this.load = false
      }
      else
      {
        this.load = true;
        this.spin.hide()
        // this.resMes = 'There is no Following persons! Please search and follow any others!'
        // document.getElementById("openModalButton").click();
      }
    })
  }

  following(id, index, type)
  {
    id = id.split('@')[1]
    this.ms.unfollow(id).then(res => { 
      console.log(res)
      if(res['Error'])
      {
        if(res['errMesg'] == undefined)
          res['errMesg'] = 'Internal Server Error'
        console.log(res['errMesg'])
        this.resMes = 'Error Message: '+res['errMesg']
        document.getElementById("openModalButton").click();
      }
      else
      {
        console.log(res['transId'])
        this.resMes = 'Your Unfollow data is submitted into blockchain' // 'Transaction ID: '+res['transId']
        document.getElementById("openModalButton").click();
        if(type == 1)
          this.Following[index].status = false;
        else
        {
          this.search_people[index].status = false;
          for(let i=0; i< this.Following.length; i++)
          {
            if(this.Following[i].Acc_id == this.search_people[index].Acc_id)
              this.Following[i].status = false;
          }
        }
        this.ms.Acc_Profile[0].following -= 1
      }
    }).catch(err =>  {
      console.log(err)
    })
  }

  follow(id, index, type)
  {
    id = id.split('@')[1]
    this.ms.follow(id).then(res => { 
      console.log(res)
      if(res['Error'])
      {
        if(res['errMesg'] == undefined)
          res['errMesg'] = 'Internal Server Error'
        console.log(res['errMesg'])
        this.resMes = 'Error Message: '+res['errMesg']
        document.getElementById("openModalButton").click();
      }
      else
      {
        console.log(res['transId'])
        this.resMes = 'Your Follow data is submitted into blockchain' // 'Transaction ID: '+res['transId']
        document.getElementById("openModalButton").click();
        if(type == 1)
          this.Following[index].status = true;
        else
        {
          this.search_people[index].status = true;
          let flag = 0;
          for(let i=0; i< this.Following.length; i++)
          {
            if(this.Following[i].Acc_id == this.search_people[index].Acc_id)
            {
              flag = 1;
              this.Following[i].status = true;
            }
          }
          if(flag == 0)
            this.Following.push(this.search_people[index])
        }
        this.ms.Acc_Profile[0].following += 1
      }
    }).catch(err =>  {
      console.log(err)
    })
  }

  search(text){
    this.ms.SearchPeople(text).then(res => {
      this.search_people = res;
    })
  }

  otherUser(name)
  {
    this.ms.setOtherUserName(name.split('@')[1])
    this.router.navigate([{outlets: {primary: ['userpost'], feed: ['userprofilelist']}}]);
  }

  goBack()
  {
    this.ms.goBack();
  }

}